.infoOuterContainer {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
  align-items: flex-start;
  gap: 10px;
  /* border: 2px solid #42484D; */
  border-radius: 20px;
  /* background-color: rgba(0, 0, 0, 0.2); */
  padding: 10px;
  /* margin-bottom: 300px; */
}

.infoOuterContainerRight {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 10px;
  /* border: 2px solid #42484D; */
  border-radius: 20px;
  /* background-color: rgba(0, 0, 0, 0.2); */
  padding: 10px;
  /* margin-bottom: 500px; */
}

.spacerDiv {
  height: 300px;
}

.spacerDivSmall {
  height: 100px;
}

.textContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.photoContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  /* margin: 20px; */
}

.slotContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 300px;
}



.cellTitleText {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  color: #00e5ff;
  filter: drop-shadow(0px 0px 3px #00e5ff);
  /* color:#CE5438; */
  font-size: 2.1em;
  text-align: center;
  width: 100%;
  gap: 10px;
  /* margin-bottom: 20px; */
}

.cellContentText {
  color: #ffffff;
  font-size: 1.2em;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  margin-top: 5px;
}

#meHeadshot {
  transform: scaleX(-1);
  border-radius: 50%;
}

.fillLineHorizontal {
  background-color: rgb(0, 229, 255);
  /* filter: drop-shadow(0px 0px 5px rgb(0, 229, 255)); */
  height: .5px;
  flex-grow: 1;
}

.fillLineVertical {
  background-color: rgb(0, 229, 255);
  filter: drop-shadow(0px 0px 5px rgb(0, 229, 255));
  min-width: 1px;
  flex-grow: 1;
  min-height: 100%;
}

.cellContentContainer {
  display: flex;
  gap: 20px;
}



.skillTable {
  /* display: flex; */
  flex-wrap: wrap;
  max-width: 400px;
  min-width: 150px;
}


.skillTable, tr, td {
  padding: 4px 10px;
  color: #ffffff;
  font-size: 1.05em;
  /* border: 1px solid; */
}

#highlightImage2 {
  border-radius: 6px;
  /* max-width: 420px; */
  /* max-width: 90vw; */
  max-width: 100%;
  height: 100%;
  filter: drop-shadow(0px 0px 5px #00e5ff);

  /* min-width: auto\9; */
}

#image2container {
  min-width: 300px;
  max-width: auto;
  display: flex;
  justify-content: center;
}


.cellContent2 {
  /* background-color: #CE5438; */
  color: #ffffff;
  font-size: 1.2em;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  margin-top: 5px;
}

#subNameCenter {
  color: rgb(0, 229, 255);
  filter: drop-shadow(0px 0px 5px #00e5ff);
  /* color: #CE5438; */
  font-size: 2.8em;
  font-family: 'Montserrat', sans-serif;
  font-weight: 200;
  margin-top: 5px;
  text-align: center;
  margin-bottom: 60px;
}

@media screen and (orientation: portrait) {
  .infoOuterContainer,
  .infoOuterContainerRight {
    flex-direction: column;
  }
  .slotContainer {
    height: 100%;
  }
  .skillTable, tr, td {
    padding: 0px 0px;
  }
}
